


#MyMap iframe {
  width: 100%;
  height: 1000px;
  
}

.ListTown{
  display: flex;
  flex-wrap: wrap;
}

.town-btn{
 
  background-color: rgb(233, 233, 233);
  border-radius: 5px;
  margin: 5px;
  width: 200px;
  height: 100px;
  text-align: center;
  line-height: 100px;
}

.selectedTown {
  background-color: #000;
  color: #fff;
}

footer{
  background-color: #0D314F;
  height: 130px;
  padding-top: 30px;
}

footer a, footer a:hover {
  color: #fff;
  text-decoration: none;
}

#Top .fa-arrow-circle-up {
  width: 50px;
  height: 50px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: #979696;
  opacity: .5;
  cursor: pointer;
}

@media (min-width: 641px){
  #MyMap iframe {
    width: 100%;
    height: 1000px;
  }

}